<template>
  <v-container class="fill-height background" >
    <v-card 
        class="d-flex justify-center flex-wrap mb-6 main-card"
        flat
        outlined
      >
      <v-card 
      v-for="(item) in media_list"
          :key="item"
        class="video-card"
        :elevation="16"
        >
        <player :streamName=item>
        </player>
      </v-card>
      </v-card>

  </v-container>

</template>
<script>
import player from './player.vue'
const axios = require('axios');
export default {
  name: "preview",
  filters: {
  },
  components: { player},
  data() {
    return {
      media_list: ''

    };
  },
  mounted() {
    this.fetch_medias();
  },
  methods: {
    reload () {
    this.fetch_medias();
   },
    fetch_medias () {
    axios.get('/api/get_medias').then(res=>{
        this.media_list = res.data;
    }).catch(function (error) {
      console.log("error init." +error)
    });
   }
  },
  computed: {
  },
};
</script>
<style scoped>
.background {
  background-color:whitesmoke;
  background-image: url('../assets/bk.png');
	background-size: contain;
	background-repeat: no-repeat;
	width: 100%; 
  height: 100%;
  margin: 0;
  margin-right:0;
  max-width: 2000px;
  padding-right:40px;
  padding-left:40px;
}
.main-card{
  background-color: transparent;
  margin:0;
  border: 0;
}

.video-card{
  background-color: dimgray;
  margin:3px;
  padding:0;
}

</style>
