<template>
  <v-container class="main"
  v-on:mouseover ="mouse_in_player"
    v-on:mouseout="mouse_leave_player">
    <video ref="videoElement" style="width:100%; height:16em;" autoplay muted
    @playing="play_state='playing'"
    @ended="play_state='stopped'"></video>
    <div class="video-cover" 
    v-on:click="click_cover"
>
        <img
          v-show="play_state != 'playing'"
          src="../assets/video/play_fill.svg"
        >
        <img
          v-show="play_state == 'playing'  && is_mouse_in_player" 
          src="../assets/video/close_fill.svg"
        >
        <v-card-title  v-show="play_state != 'playing'" class="pt-1 video-cover-text">
      {{streamName}}
    </v-card-title>
    </div>
  </v-container>
</template>
<script>

import mpegts from './mpegts.js'

export default {
  name: "mpegts-player-components",
  filters: {
  },
  props: {
    streamName: {
      type: String,
      required: true
    }
  },
  components: {},
  data() {
    return {
      mpegtsPlayer: null,
    play_state: "stopped",
    is_mouse_in_player:false
    };
  },
  mounted() {

  },
  beforeDestroy() {
    this.close();
  },
  methods: {
    click_cover(){

      if(this.play_state == 'stopped'){
        this.play();
      }

      if(this.play_state == 'playing'){
        this.close();
      }

    },
    close(){
      if (this.mpegtsPlayer != null){
        this.mpegtsPlayer.unload();
          this.mpegtsPlayer.detachMediaElement();
          this.mpegtsPlayer.destroy();
          this.mpegtsPlayer = null;
          this.play_state = 'stopped';
          console.log("closed...");
      } 
    },
    mouse_in_player(){
      this.is_mouse_in_player=true;
    },
    mouse_leave_player(){
      this.is_mouse_in_player=false;
},
    play() {
      console.log("play...");
      this.player_load();
    },
    player_load() {
      var url = "ws://127.0.0.1:8894/" + this.streamName;
      var mediaDataSource = {
        type: 'flv',
        "isLive": true,
        "withCredentials": false,
        "hasAudio": false,
        "url": url
      };
      console.log('MediaDataSource', mediaDataSource);
      this.player_load_mds(mediaDataSource);
    },
    player_load_mds(mediaDataSource) {

        if (typeof this.mpegtsPlayer !== "undefined") {
        if (this.mpegtsPlayer != null) {
          this.mpegtsPlayer.unload();
          this.mpegtsPlayer.detachMediaElement();
          this.mpegtsPlayer.destroy();
          this.mpegtsPlayer = null;
        }
      }
      this.mpegtsPlayer = mpegts.createPlayer(mediaDataSource, {
        enableWorker: false,
        lazyLoadMaxDuration: 0,
        lazyLoad: false,
        seekType: 'range',
        enableStashBuffer: false,
        liveBufferLatencyChasing: true,
        autoCleanupSourceBuffer: true
      });
      this.mpegtsPlayer.attachMediaElement(this.$refs.videoElement);
      this.mpegtsPlayer.load();
    }
  },
  computed: {
  },
};
</script>
<style scoped>
.video-cover {
  height: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}

.video-cover img {
  width: 20%;
  margin-left: 40%;
}
.video-cover img:hover{
  transform: scale(1.1);
}

.video-cover-text {
  color:orange;
}

.main {
  background-color: transparent;
  margin:0;
  padding:0;
}
</style>
